*{
   margin: 0;
   padding: 0;
}

.header_outer_container {
   position: sticky;
   top: 0;
  display: flex;
   justify-content: center;
   align-items: center;
   overflow: hidden;
   /* background-color: rgb(148, 132, 132); */
   background-color: rgb(20, 20, 20);
   /* background-color: transparent; */
   height: 10vh;
   color: white;
   transition: all 0.5s;
   transition-timing-function: ease-in;
   z-index: 1;
   /* background-color: red; */
}

.nav_black{
   background-color: rgb(20, 20, 20);
}

.header_container{
   width: 90%;
   display: flex;
   justify-content: space-between;
   align-items: center;
}

.header_left ul{
   width: 140%;
   display: flex;
   justify-content: space-around;
   align-items: center;
   list-style: none;
   cursor: pointer;
   /* background-color: lightgreen; */
}

.header_right ul{
   width: 150%;
   display: flex;
   justify-content: space-around;
   align-items: center;
   list-style: none;
   cursor: pointer;
   /* background-color: lightgreen; */
}

@media screen and (max-width:768px){
   .header_outer_container{
      height: 20vh;
   }
   
   
   .header_container{
      display: flex;
      flex-direction: column !important;
   }
   
   
}